import React, { useState } from 'react';
import './SearchResult.css';
import FavoriteBorderIcon from '@material-ui/icons/FavoriteBorder';
import StarIcon from '@material-ui/icons/Star';
import { useNavigate } from 'react-router-dom';
import { Button } from '@material-ui/core';
import SearchDate from './SearchDate';
import { makeStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import ButtonBase from '@material-ui/core/ButtonBase';
import axios from 'axios';
axios.defaults.withCredentials = true;

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  paper: {
    padding: theme.spacing(1),
    margin: 'auto',
    maxWidth: 750,
  },
  image: {
    width: 350,
    height: 200,
  },
  img: {
    margin: 'auto',
    display: 'block',
    maxWidth: '100%',
    maxHeight: '100%',
  },
}));

function SearchResult({ hotel }) {
  const [serverErrors, setServerErrors] = useState('');
  const [showSearch, setShowSearch] = useState(false);
  const [adminAuth, setAdminAuth] = useState(false);
  const [signInMSG, setSignInMSG] = useState('');
  const navigate = useNavigate();
  const classes = useStyles();

  const checkAuth = async () => {
    try {
      const response = await axios(
        'https://hotel-booking-webapp.herokuapp.com/auth/check-user',
        {
          headers: { 'content-type': 'application/json' },
          method: 'GET',
        }
      );

      if (response.data.adminAuth) {
        setAdminAuth(true);
      }
    } catch (error) {
      if (error.response) {
        console.log(error.response.data);
        setServerErrors(error.response.data.message);
        console.log(serverErrors);
        setAdminAuth(false);
        setSignInMSG('Please sign in!');
      } else {
        console.log('Error', error.message);
      }
      console.log(error);
    }
  };

  const onClick = () => {
    navigate(`/admin/edit/${hotel._id}`);
  };

  React.useEffect(() => {
    checkAuth();
    const interval = setInterval(() => {
      checkAuth();
    }, 7200000);
  }, []);

  return (
    <div className={classes.root}>
      <Paper className={classes.paper}>
        <Grid container spacing={1}>
          <Grid item xs={6} style={{ display: 'flex' }}>
            <ButtonBase className={classes.image}>
              <img className={classes.img} alt='complex' src={hotel.imageUrl} />
            </ButtonBase>
          </Grid>
          <Grid item xs={6} sm container style={{ display: 'flex' }}>
            <Grid item xs container direction='column' spacing={2}>
              <Grid item xs={12}>
                <Typography gutterBottom variant='h6'>
                  {hotel.title}
                </Typography>
                <Typography variant='body2' color='textSecondary'>
                  {hotel.description}
                </Typography>
              </Grid>
            </Grid>
            <Grid item xs={12}>
              <Typography variant='h6'>${hotel.price}</Typography>
            </Grid>
            <Grid item>
              {adminAuth ? (
                <Button onClick={onClick} size='small' variant='outlined'>
                  Edit
                </Button>
              ) : null}
            </Grid>
          </Grid>
        </Grid>
      </Paper>
    </div>
  );
}

export default SearchResult;
